.header{
    background-image: url("../public/assets/idfImg.jpeg") ;
    /* url('../public/assets/idfImg.jpeg') */
    height: 300px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    /* opacity: 64%; */
}
.bg{
    min-height: 100vh;
    background-color: #FEA702;
}
.divTitle{
    height: inherit;
    display: flex;
    align-items: flex-end;
    /* align-items: center; */
    justify-content: center;
    color: white;
    font-size: 40px;
    font-weight: bold;
}
.title{
    padding-bottom: 25px;
    width: 300px;
    text-align: center;
   
}
.text{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.subTitle{
    color: black;
    display: flex;
    justify-content: center;
    text-align: center;
    padding: 25px;
    font-size: 20px 20px 20px 0px;
    font-weight: bold;
    max-width: 360px;
    text-align: center;
}
.miniTitle{
    font-size: 35px;
    font-weight: bold;
    padding-bottom: 10px;
    color:#265BA8
}
.buttons{
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
}
.buttonDiv{
    padding-top: 40px;
    display: flex;
    justify-content: center;
}
.button{
    background-color: #265BA8;
    color: white;
    font-size: 20px;
    font-weight: bold;
    height: 68px;
    width: 143px;
    border: none;
    border-radius: 20px 0px 20px 0px;
    cursor: pointer;
}
.button:active{
    opacity: 0.5;
}
.quantity{
    background-color: white;
    height: 68px;
    width: 143px;
    color: #2E8BFF;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px 0px 20px 0px;
    font-size: 20px;
    font-weight: bold;
}
